'use client'
import { useState, useEffect } from 'react'
import { supabase } from '@/lib/supabaseClient'

export default function Home() {
  const [url, setUrl] = useState('')
  const [title, setTitle] = useState('')
  const [bookmarks, setBookmarks] = useState<any[]>([])

  // Fetch bookmarks for the logged-in user
  useEffect(() => {
    const fetchBookmarks = async () => {
      const {
        data: { user }
      } = await supabase.auth.getUser()

      if (user) {
        const { data, error } = await supabase
          .from('bookmarks')
          .select('*')
          .eq('user_id', user.id)
          .order('created_at', { ascending: false })

        if (!error) setBookmarks(data || [])
      }
    }

    fetchBookmarks()
  }, [])

  // Save bookmark
  const saveBookmark = async () => {
    const {
      data: { user }
    } = await supabase.auth.getUser()

    if (user) {
      const { error } = await supabase.from('bookmarks').insert([
        {
          id: crypto.randomUUID(), // generate UUID for id
          url,
          title,
          user_id: user.id
        }
      ])
      if (error) {
        console.error('Error saving bookmark:', error.message)
      } else {
        setUrl('')
        setTitle('')
        alert('Bookmark saved!')
      }
    }
  }

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-10">
      <h1 className="text-3xl font-bold mb-6">ðŸ“š Smart Bookmark App</h1>

      <input
        type="text"
        placeholder="Enter website URL..."
        value={url}
        onChange={(e) => setUrl(e.target.value)}
        className="border p-2 rounded w-80 mb-4"
      />

      <input
        type="text"
        placeholder="Enter title (optional)..."
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        className="border p-2 rounded w-80 mb-4"
      />

      <button
        onClick={saveBookmark}
        className="bg-blue-500 text-white px-4 py-2 rounded"
      >
        Save Bookmark
      </button>

      <ul className="mt-6 w-80">
        {bookmarks.map((b) => (
          <li key={b.id} className="border-b py-2">
            <strong>{b.title || 'Untitled'}</strong> â€” {b.url}
          </li>
        ))}
      </ul>
    </main>
  )
}
